@import '../../../scss/mixins';
@import '../../../scss/variables';

.slide {
  height: 100vh;
  width: 100%;
  @include flex-center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  z-index: $layer-1;

  &:after {
    content: '';
    height: 100%;
    width: 100%;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
  }
}